body {
  padding: 0;
  margin: 0;
  background-color: #1b2d37; }

.header-logo__image {
  display: block;
  position: relative;
  margin-top: 34px;
  margin-left: 14px; }

.header-logo {
  display: flex; }

.header-logo div {
  margin-top: 39px;
  margin-left: 14px; }

.header-logo__title {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  color: #ffffff;
  margin: 0; }

.header-logo__subtitle {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin: 0;
  color: #788891; }

@media (max-width: 1023px) {
  .header-logo__image {
    margin-top: 20px;
    margin-left: 6px; }
  .header-logo div {
    margin-top: 27px;
    margin-left: 14px; } }

@media (max-width: 767px) {
  .header-logo__image {
    width: 38px;
    height: 38px;
    margin-top: 16px;
    margin-left: -3px; }
  .header-logo__title,
  .header-logo__subtitle {
    display: none; } }

.main-navigation {
  display: flex;
  align-items: center;
  padding-top: 34px; }

@media (max-width: 1023px) {
  .main-navigation {
    padding-top: 24px; } }

.contacts-list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  margin-right: 50px; }

.contacts-list__link {
  font-family: "PTMono", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: normal;
  line-height: 18px;
  text-decoration: none;
  color: #ffffff; }

.contacts-list__item {
  margin-right: 120px;
  position: relative; }
  .contacts-list__item:last-child {
    margin-right: 0; }

.contacts-list__link-icon--email {
  position: absolute;
  width: 18px;
  height: 14px;
  top: 1px;
  left: -38px; }

.contacts-list__link-icon--phone {
  position: absolute;
  width: 18px;
  height: 18px;
  top: -2px;
  left: -32px; }

@media (max-width: 1023px) {
  .contacts-list__item {
    margin-right: 0;
    position: relative; }
  .contacts-list__link span {
    display: none; }
  .contacts-list__link-icon--email {
    width: 35px;
    height: 18px;
    top: -13px;
    left: -41px; }
  .contacts-list__link-icon--phone {
    position: absolute;
    width: 32px;
    height: 24px;
    top: -16px;
    left: 18px; } }

@media (max-width: 767px) {
  .contacts-list__link-icon--email {
    width: 35px;
    height: 18px;
    top: -14px;
    left: -23px; }
  .contacts-list__link-icon--phone {
    position: absolute;
    width: 32px;
    height: 24px;
    top: -17px;
    left: 28px; } }

.callback-button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 180px;
  height: 45px;
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  background-color: transparent;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 5px;
  outline: none;
  color: #ffffff;
  cursor: pointer; }
  .callback-button:hover, .callback-button:focus {
    background-color: #ffffff;
    border-color: #00b2ff;
    color: #00b2ff; }

@media (max-width: 1023px) {
  .callback-button {
    display: none; } }

footer {
  background-color: #CDCDCD; }

.footer-logo__image {
  display: block;
  position: relative;
  padding-top: 64px;
  margin-left: 14px; }

.footer-logo {
  display: flex; }

.footer-logo div {
  padding-top: 70px;
  margin-left: 14px; }

.footer-logo__title {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 25px;
  line-height: 29px;
  color: #203c42;
  margin: 0; }

.footer-logo__subtitle {
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
  margin: 0;
  color: #788891;
  padding-top: 2px; }

@media (max-width: 1023px) {
  .footer-logo-block {
    display: flex;
    outline: 1px solid red;
    width: 100%; } }

.social-list {
  display: flex; }

.sections-website {
  padding-top: 64px;
  margin-left: 51px; }

.sections-website__title {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #1b2d37;
  margin: 0; }

.sections-website-list {
  list-style: none;
  margin: 0;
  padding: 0;
  columns: 2;
  margin-top: 24px; }

.sections-website-list__item {
  font-family: "PTMono", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 25px; }

.sections-website-list__link {
  text-decoration: none;
  color: #1b2d37;
  opacity: 0.5; }

@media (max-width: 1023px) {
  .sections-website {
    margin-left: 0; }
  .sections-website-list {
    columns: 1; } }

.our-office {
  padding-top: 64px; }

.our-office__title {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 16px;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #1b2d37;
  margin: 0;
  margin-left: 2px; }

.our-office-list {
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 24px; }

.our-office-list__item {
  font-family: "PTMono", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 15px;
  line-height: 25px;
  color: #1b2d37;
  opacity: 0.5; }

.our-office-list__link {
  text-decoration: none;
  color: #1b2d37;
  opacity: 0.5; }

.second-line-footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 60px; }

.second-line-footer__rights,
.second-line-footer__working-activity,
.second-line-footer__confidentiality-conditions,
.second-line-footer__developer {
  font-family: "PTMono", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 13px;
  line-height: 24px;
  letter-spacing: 0.04em;
  text-transform: uppercase;
  text-decoration: none;
  color: #1B2D37;
  opacity: 0.5; }

.first-line-footer {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.wrapper {
  max-width: 1423px;
  margin: 0 auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  outline: 1px solid red; }
  .wrapper--footer {
    flex-direction: column; }
